import React, { useState, useLayoutEffect } from 'react';

export default function Video({ className, ratio, id: videoId, ...rest }) {
  const [height, setHeight] = useState('auto');
  const id = videoId || 'videoFrame';

  useLayoutEffect(() => {
    function updateSize() {
      const videoFrame = document.querySelector(`#${id}`);

      if (videoFrame) {
        setHeight(`${videoFrame.clientWidth / ratio}px`);
      }
    }
    window.addEventListener('resize', updateSize);
    updateSize();
    return () => window.removeEventListener('resize', updateSize);
  }, [ratio, id]);

  return (
    <>
      <iframe
        frameBorder="0"
        allow="autoplay; fullscreen"
        allowFullScreen
        height={height}
        {...rest}
        className={`video ${className ? className : ''}`}
        id={id}
      />
      <style jsx>{`
        .video {
          box-shadow: 0px 42.4071px 84.8142px -42.4071px rgb(0 0 0 / 30%),
            0px 70.6785px 141.357px -28.2714px rgb(50 50 93 / 25%),
            inset 0px 1.41357px 1.41357px rgb(143 153 174 / 5%);
          border-radius: 8px;
        }
      `}</style>
    </>
  );
}
